.accord input {
  display: none; }

.accord-content {
  display: none; }

.accord input:checked ~ .accord-content {
  display: block; }

.accord label {
  cursor: pointer;
  font-weight: 700;
  display: block;
  margin: 9px 0 -2px;
  padding: 0; }
  .accord label::before {
    content: "";
    float: right;
    width: 19px;
    height: 13px;
    background: url("../img/arrow-down.svg") 0 0 no-repeat;
    margin-top: 5px; }

.accord input:checked + label:before {
  transition: transform .35s;
  transform: rotate(180deg); }

.accord input:checked + label + .accord-content {
  height: auto;
  color: #2E3A59; }

.accord input:checked + label {
  color: #00C368; }

